
.section {
    max-height: 100svh;
    width: 100svw;
    font-size: 1.5rem;
    background-color: hsl(0, 0%, 45%);
    color: hsl(0,0%, 100%);
    padding: 2rem 4rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.section-line {
    height: 0;
}
.section-upper-box {
    margin-left: 1rem;
    margin-bottom: 2rem;
}
.section-upper-text {
    font-size: 0.6em;
    margin-left: 0.4rem;
    font-weight: 900;
}
.section-middle-data {
    display: flex;
    gap:2rem;
    padding-left: 10rem;
    padding-top: 1rem;
    height: max-content;
}
.section-middle-data-text {
    width: 35svw;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}
.section-social-media{
    display: flex;
    justify-content: flex-start;
    gap: 1rem;
    margin-top: 0.5rem;
    flex-wrap: wrap;
    height: max-content;
}

.section-social-media img{
    height: 3rem;
    width: 3rem;
}


.section-middle {
    display: flex;
    margin: 3rem 2rem 8rem 2rem; 
}

.section-midddle-text {
    font-size: 4rem;
    text-transform: capitalize;
    color: hsl(0, 0%, 80%);
    width: 20rem;
}
.section-middle-download-title {
    color: hsl(0, 0%, 85%);
}
.section-middle-image-img{
    font-size: 1rem;
    height: 10rem;
}
.section-child {
    max-width: 90svw;
}
.section-middle-download{
    font-size: 1.25em;
    text-transform: uppercase;
}

.section-lower-text {
    font-size: 0.6em;
    color: hsl(0,0%, 60%);
    word-spacing: 4px;
    letter-spacing: 1.25px;
}

@media screen and (max-width:800px){
    .section>*{
        display: block;
        margin-left: 0;
        padding-left: 0;
    }
    .section-upper-text{
        font-size: 1.5rem;
    }
    .section-midddle-text{
        font-size: 3rem;
    }
    .section-middle-data-text{
        margin: 3rem 0 0 0;
    }
    .section-middle-data{
        display: block;
        padding: 0;
    }
    .section-middle-data-text>* {
        margin: 0.5rem auto ;
        width: 80svw;
    }
    .section-midddle-text{
        width: 80svw;
        margin: 2rem 0;
    }
    .section-upper-box{
        margin-bottom: 2rem;
    }
    .sidebar-open {
        width: 50%;
    }
}